import React, { useRef, useEffect } from 'react';
import './slider.style.scss';

interface SliderComponentProps {
  theme: string;
  disabled: boolean;
  onSendProgress?: (value: string) => void; // Made this prop optional
}

const SliderComponent: React.FC<SliderComponentProps> = ({ theme, disabled, onSendProgress }) => {
  const scrollbarRef = useRef<HTMLInputElement>(null);
  const scrollTrackRef = useRef<HTMLDivElement>(null);

  // Update the scroll track width when the component is mounted
  useEffect(() => {
    updateScrollTrack();
  }, []);

  // Update scroll track width and invoke the onSendProgress callback
  const updateScrollTrack = () => {
    if (scrollbarRef.current && scrollTrackRef.current) {
      const value = scrollbarRef.current.value;
      scrollTrackRef.current.style.width = `${value}%`;
      (onSendProgress || (() => {}))(value); 
    }
  };

  // Handle input event
  const onInput = () => {
    if (scrollbarRef.current && scrollTrackRef.current) {
      const value = scrollbarRef.current.value;
      scrollTrackRef.current.style.width = `${value}%`;
      (onSendProgress || (() => {}))(value);
    }
  };

  // Determine the CSS class based on the disabled state
  const classes = disabled
    ? `slider--${theme} slider--disabled--${theme}`
    : `slider--${theme}`;

  return (
    <div className={`slider-container ${classes}`}>
      <input
        ref={scrollbarRef}
        onInput={onInput}
        disabled={disabled}
        type="range"
        className="slider"
        id="myRange"
      />
      <div ref={scrollTrackRef} className="scroll-div"></div>
    </div>
  );
};

export default SliderComponent;
